		initWaterfall() {
			let leftHeightTotal = 0
			let rightHeightTotal = 0
			
			const query = uni.createSelectorQuery().in(this);
			query.selectAll('.peopleFall-item').boundingClientRect(data => {
				
				data.forEach(item=>{
